

let contactBtn = document.querySelector('.contacts');
let contactPopup = document.querySelector('.contacts-popup');

contactBtn.addEventListener('click', () => {
    contactPopup.classList.toggle('contacts-popup-active');
});

$(function () {
    $('.slides').slick({
        prevArrow: ('.btn-prev'),
        nextArrow: ('.btn-next'),
    });

    $('.photos__slider').slick({
        arrows: false,
        infinite: true,
        autoplay: true,
        autoplaySpeed: 3000,
        slidesToShow: 2,
        variableWidth: true
    });

})

/*
    video-click
*/

let videoList = document.querySelector('.video__list');
let videoImg = document.querySelector('.video-item');
let videoItem = document.querySelectorAll('.video__item-link');
let videoIframe = document.querySelectorAll('.video__item-iframe');

videoList.addEventListener('click', videoClick);
for (let i = 0; i < videoItem.length; i++) {
    videoItem[i].addEventListener('click', () => {
        videoImg.scrollIntoView({ block: "center", behavior: "smooth" });
    });
}


function videoClick(Event) {

    for (let i = 0; i < videoItem.length; i++) {
        videoIframe[i].style.display = 'none';
        videoItem[i].classList.remove('questions-el-active');
        videoIframe[i].classList.remove('video-active');
    }
    if (Event.target == videoList) {

    }
    else {
        let videoId = Event.target.id;
        videoItem[videoId].classList.add('questions-el-active');
        videoIframe[videoId].style.display = 'block';
    }
};
